import React from 'react'
import './Headder.css'
import dress from "../../../images/chicken_kurti_icon.png"

export default function Headder(props)
{
    return <div id={props.id}>
    <img src={dress} alt="icon" className='icon'></img><div className='title'>Chicken Kurtis Inventory</div>
    </div>
} 
