import React from 'react';
import './App.css';
import Footer from './components/footer/Footer';
import Headder from './components/headding/headder/Headder';
import Navigation from './components/headding/navigation/Navigation';
import Main from './components/main/Main';
import Offer from './components/offers/Offer';
import html2pdf from 'html2pdf.js'; // Import html2pdf.js

function App() {

  const handleDownloadPDF = () => {
    const element = document.getElementById('main'); // Get the div with class 'App'
    
    const opt = {
      margin:       1,
      filename:     'react_app.pdf',
      image:        { type: 'image', quality: 0.5 },
      html2canvas:  { scale: 2 },
      jsPDF:        { unit: 'in', format: 'a4', orientation: 'portrait' }
    };

    html2pdf().from(element).set(opt).save(); // Generate the PDF and trigger download
  };

  return (
    <div className="App" id="app">

      {/* top most div for sale offer */}
      <div id="offers" className='offers'>
        <Offer id="offer"/>
      </div>

      {/* headder and navigation tab div */}
      <div id="headding" className="headding" >
        <Headder id="headder" />
        <Navigation  id="navigation" />
      </div>
      
      {/* main div */}
      <Main id="main"/>

      {/* Footer div */}
      <Footer id="footer" />
      {/* Button to download PDF */}
      <button onClick={handleDownloadPDF}>Download PDF</button>
    </div>
  );
}

export default App;
